body {
  font: 14px "Century Gothic", Futura, sans-serif;
  margin: 20px;
  background-color: grey;
}

ol, ul {
  padding-left: 30px;
}

.square {
  background: #fff;
  border: 1px solid #999;
  float: left;
  font-size: 24px;
  font-weight: bold;
  line-height: 34px;
  height: 34px;
  margin-right: -1px;
  margin-top: -1px;
  padding: 0;
  text-align: center;
  width: 34px;
}

.square:focus {
  outline: none;
}

.workDisplay {
  float: left;
  margin-right: 20px;
}

.bmpPreview {
  float: right;
  position: relative;
  margin-right: 20px;
}

.panelDisplay {
  float: right;
}

#previewComponent{
  position: absolute;
}

.workImage {
}


.hide {
  display: none;
}
